var after = $('#after-value');
var afterText = $('#after-text');

$("#nav-1 a").on("click", function(e) {
  e.preventDefault()
  var position = $(this)
  .parent()
  .position();
  var width = $(this)
  .parent()
  .width();
  $("#nav-1 .slide1").css({ opacity: 1, left: +position.left, width: width });
  var newValue = $(this).data('value')
  
  after.animate({
    opacity: 0
  }, 250);
  afterText.animate({
    opacity: 0
  }, 250);
  setTimeout(function(){
    afterText.html(donContent[newValue]['text']);
    after.text(newValue + "");
  }, 250);
  after.animate({
    opacity: 1
  }, 250);
  afterText.animate({
    opacity: 1
  }, 250);

  $(this).parent().parent().find(".active").removeClass("active");
  $(this).addClass('active');
});

$("#nav-1 a").on("mouseover", function() {
  var position = $(this)
  .parent()
  .position();
  var width = $(this)
  .parent()
  .width();
  $("#nav-1 .slide2")
  .css({
    opacity: 1,
    left: +position.left,
    width: width
  })
  .addClass("squeeze");
});

$("#nav-1 a").on("mouseout", function() {
  $("#nav-1 .slide2")
  .css({ opacity: 0 })
  .removeClass("squeeze");
});

var currentWidth = $("#nav-1 .nav")
.find(".active")
.parent("li")
.width();
var current = $(".nav .active").position();
$("#nav-1 .slide1").css({ left: +current.left, width: currentWidth });


$(window).resize(function() {
  var item = $("#nav-1 a.active")
  var position = $(item)
  .parent()
  .position();
  var width = $(item)
  .parent()
  .width();
  $("#nav-1 .slide1").css({ opacity: 1, left: position.left, width: width });
})